    //////////////////// Init Select Option  ////////////////////
    private async initSelectOption(): Promise<void> {
        await this.initSelectOptionDoor();
        await this.initSelectOptionSchedule();
    }

    private async initSelectOptionSchedule(): Promise<void> {
        this.selectOptionItem.schedule = [];

        let data: any[] = JSON.parse(JSON.stringify(ACCESS_SCHEDULES));
        data.forEach((item) =>
            this.selectOptionItem.schedule.push({
                objectId: item.objectId,
                name: item.name,
            }),
        );
    }

    private async initSelectOptionDoor(): Promise<void> {
        this.selectOptionItem.area = [];
        this.selectOptionItem.allArea = [];

        let data: any[] = JSON.parse(JSON.stringify(ACCESS_AREA));
        (data || []).forEach((item) => this.selectOptionItem.area.push({ objectId: item.objectId, name: item.name }));

        this.selectOptionItem.allArea = this.selectOptionItem.area;
    }

    //////////////////// Table ////////////////////
    private async tableGetApiData(tableParam: IComponent.ITableParam): Promise<void> {
        let filterData: IFilterData = this.filterData;
        this.formItem.isClearState = false;

        if (tableParam && tableParam.page && tableParam.pageSize) {
            this.tableItem.paging.page = tableParam.page ? tableParam.page : this.tableItem.paging.page;
            this.tableItem.paging.pageSize = tableParam.pageSize ? tableParam.pageSize : this.tableItem.paging.pageSize;

            this.tableApiParam.paging = {
                page: this.tableItem.paging.page,
                pageSize: this.tableItem.paging.pageSize,
            };
        }

        if (tableParam && tableParam.sortingField && tableParam.sortingOrder !== undefined) {
            this.tableApiParam.sorting = {
                field: tableParam.sortingField,
                order: tableParam.sortingOrder,
            };
        } else {
            delete this.tableApiParam.sorting;
        }

        if (this.filterData.keyword) {
            this.tableApiParam.keyword = this.filterData.keyword;
        } else {
            delete this.tableApiParam.keyword;
        }

        if (filterData.area && filterData.area.objectId) {
            this.tableApiParam.areaId = filterData.area.objectId;
        } else {
            delete this.tableApiParam.areaId;
        }

        let param = JSON.parse(JSON.stringify(this.tableApiParam));

        let url: string = '';

        if (this.formItem.isSearch) {
            param.paging.page = 1;
        }

        this.loading.show();

        let data = this.getFakeData(tableParam);
        this.tableSetData(data);

        this.formItem.isClearState = true;

        this.loading.hide();
    }

    private getFakeData(tableParam: IComponent.ITableParam): any {
        let data: any[] = JSON.parse(JSON.stringify(ACCESS_GROUPS));

        // keyword
        if (!!this.tableApiParam?.keyword) {
            const keywordFields: string[] = ['name'];
            let keyword: string = this.tableApiParam.keyword.toLowerCase();
            data = data.filter((item) => {
                let isMatch: boolean = false;

                for (let field of keywordFields) {
                    let target: string = item[field];
                    if (!!target && target.toLowerCase().includes(keyword)) {
                        isMatch = true;
                        break;
                    }
                }

                return isMatch;
            });
        }

        // door
        if (!!this.tableApiParam?.areaId) {
            data = data.filter((item) => {
                let areaIds: string[] = (item.doors || []).map((door) => door.area.objectId);
                return areaIds.indexOf(this.tableApiParam.areaId) > -1;
            });
        }

        // sorting
        if (!!tableParam && !!tableParam.sortingField && tableParam.sortingOrderEnum !== ETableSorting.none) {
            let sortingField: string = tableParam.sortingField;
            data = data.sort((a, b) => {
                if (tableParam.sortingOrder === 0) {
                    if (typeof a[sortingField] === 'number') {
                        return a[sortingField] - b[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return 1;
                    if (a[sortingField] < b[sortingField]) return -1;
                } else {
                    if (typeof a[sortingField] === 'number') {
                        return b[sortingField] - a[sortingField];
                    }

                    a[sortingField] = a[sortingField] || '';
                    b[sortingField] = b[sortingField] || '';
                    if (a[sortingField] > b[sortingField]) return -1;
                    if (a[sortingField] < b[sortingField]) return 1;
                }

                return 0;

                return 0;
            });
        }

        // pagination
        let pageSize: number = tableParam?.pageSize || this.tableApiParam.paging.pageSize;
        let total: number = data.length;
        let totalPages: number = Math.ceil(total / pageSize);
        let page: number = tableParam?.page || this.tableApiParam.paging.page;
        if (page > totalPages) {
            page = totalPages || 1;
        }

        this.tableItem.paging.pageSize = pageSize;
        this.tableItem.paging.page = page;
        this.tableItem.paging.totalPages = totalPages;
        this.tableItem.paging.total = total;

        data = data.slice((page - 1) * pageSize, page * pageSize);

        return data;
    }

    private async tableReload(tableParam: IComponent.ITableParam): Promise<void> {
        if (tableParam) {
            this.tableParam.page = tableParam.page ? tableParam.page : this.tableItem.paging.page;
            this.tableParam.pageSize = tableParam.pageSize ? tableParam.pageSize : this.tableItem.paging.pageSize;
            this.tableParam.sortingField = tableParam.sortingField;
            this.tableParam.sortingOrder = tableParam.sortingOrder;

            await this.tableGetApiData(this.tableParam);
        }
    }

    private tableSetData(result: any[]): void {
        let tableData: ITableData[] = [];

        (result || []).forEach((element) => {
            tableData.push({
                objectId: element.objectId ? element.objectId : '',
                name: element.name ? element.name : '',
                doors: element.doors ? element.doors : [],
                remark: element.remark ? element.remark : '',
            });
        });

        this.tableItem.datas = tableData;
    }

    private titleDetailTitle(): string {
        return `${this.formData.name ? this.formData.name : ''} Detail Table`;
    }

    private tableDeleteButtonDisabled(): boolean {
        return this.tableSelectedData.length > 0 ? false : true;
    }

    private tableClickCheckbox(selectedData: ITableData[]): void {
        this.tableSelectedData = selectedData;
    }

    //////////////////// Search ////////////////////
    private async searchInputKeyword(): Promise<void> {
        await this.tableReload(this.tableParam);
    }

    private async searchSelectSingle(singleData: IComponent.ISelectionBase): Promise<void> {
        this.filterData.area = singleData;
        await this.searchData(this.tableParam);
    }

    private async searchData(tableParam: IComponent.ITableParam): Promise<void> {
        this.formItem.isSearch = true;
        await this.tableGetApiData(tableParam);
        this.formItem.isSearch = false;
    }

    //////////////////// Form Show ////////////////////
    private formTitle(title: string): string {
        return title ? title : this.$i18n.Form_FilterCreateNewData;
    }

    private formCreate(): boolean {
        return this.pageItem.action === EPageAction.create;
    }

    private formEdit(): boolean {
        return this.pageItem.action === EPageAction.edit;
    }

    private formPreview(): boolean {
        return this.pageItem.action === EPageAction.preview;
    }

    private formSaveDisable(): boolean {
        let data: IFormData = this.formData;
        const disabled: IFormButtonDisabled = this.formButtonDisabled;

        if (disabled.name || (data.areaIds && data.areaIds.length === 0) || (data.scheduleIds && data.scheduleIds.length === 0)) {
            return true;
        }

        return false;
    }

    //////////////////// Form Set Data ////////////////////

    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
        this.formItem.isClearState = false;
    }

    private formConfigValues(value: any, disabledBtuuon: boolean, id: string): void {
        Object.keys(this.formData).forEach((key) => {
            if (id === key) this.formData[id] = value;
        });

        Object.keys(this.formButtonDisabled).forEach((key) => {
            if (id === key) this.formButtonDisabled[id] = disabledBtuuon;
        });
    }

    private formDisabledValues(value: boolean, id: string): void {
        Object.keys(this.formDataDisabled).forEach((key) => {
            if (id === key) this.formDataDisabled[id] = value;
        });
    }

    private async formConfigSet(data: any = undefined): Promise<void> {
        if (data) await this.formDataSet(data);
        switch (this.pageItem.action) {
            case EPageAction.create:
            case EPageAction.edit:
            case EPageAction.preview:
                break;
            default:
                break;
        }
    }

    private async formDataSet(data: ITableData): Promise<void> {
        this.formData.objectId = data.objectId;
        this.formData.name = data.name;
        this.formData.remark = data.remark;
        this.formData.areaIds = [];

        this.formData.areaIds = [];
        this.formData.scheduleIds = [];
        this.selectOptionItem.area = [];

        (data.doors || []).forEach((item, index) => {
            this.formData.areaIds.push(data.doors[index].area);
            this.formData.scheduleIds.push(data.doors[index].schedule);
        });

        this.selectOptionItem.area = (this.selectOptionItem.allArea || []).filter(
            (item) => !this.formData.areaIds.some((ele) => ele.objectId === item.objectId),
        );
        this.formItem.isClearState = false;
    }

    //////////////////// Form Schedules ////////////////////

    private formAddSchedulesDisabled() {
        return !this.formData.areaId || !this.formData.scheduleId ? true : false;
    }

    private formAddSchedules() {
        this.formData.areaIds.push(this.formData.areaId);
        this.formData.scheduleIds.push(this.formData.scheduleId);
        this.selectOptionItem.area = [];

        // let add = arr2.filter((item) => !arr1.some((ele) => ele.id === item.id));
        if (Array.isArray(this.selectOptionItem.allArea)) {
            this.selectOptionItem.area = this.selectOptionItem.allArea.filter(
                (item) => !this.formData.areaIds.some((ele) => ele.objectId === item.objectId),
            );
        }

        this.formData.areaId = null;
        this.formData.scheduleId = null;
    }

    private formRemoveSchedules(index: number) {
        for (let i in this.formData.areaIds) {
            if (parseInt(i, 10) === index) {
                this.selectOptionItem.area.push(this.formData.areaIds[index]);
                this.formData.areaIds.splice(index, 1);
                this.formData.scheduleIds.splice(index, 1);
            }
        }
    }

    //////////////////// Modify API (save, delete, test)  ////////////////////

    private async dataDelete(rowData: ITableData | ITableData[]): Promise<void> {
        let content: string = this.$i18n.Dialog_ConfirmDelete;
        let double_content: string = this.$i18n.Dialog_DoubleConfirmDelete;
        let title: string = this.$i18n.Dialog_Confirm;

        let tempDeleteData: any[] = Array.isArray(rowData) ? rowData : [rowData];

        let param: { data: { objectId: string[] } } = { data: { objectId: [] } };
        tempDeleteData.forEach((item) => param.data.objectId.push(item.objectId));

        // one comfirm
        await ServiceDialog.Question(this, content, title)
            .then(async (result) => {
                if (result.value) {
                    this.loading.show();

                    // modify fake data
                    if (Array.isArray(rowData)) {
                        rowData.forEach((item) => {
                            let targetIndex: number = ACCESS_GROUPS.findIndex((item) => item.objectId === item.objectId);
                            ACCESS_GROUPS.splice(targetIndex, 1);
                        });
                    } else {
                        let targetIndex: number = ACCESS_GROUPS.findIndex((item) => item.objectId === rowData.objectId);
                        ACCESS_GROUPS.splice(targetIndex, 1);
                    }

                    this.loading.hide();

                    ServiceToast.success(this.$i18n.Dialog_DeleteSuccess);
                    this.pageToList();
                    await this.tableReload(this.tableParam);
                }
            })
            .catch((e: any) => console.error(e));

