    private async dataSave(): Promise<void> {
        let data: IFormData = this.formData;
        let doors: IAccessGroupAreaSchedule[] = [];

        for (let index = 0; index < data.areaIds.length; index++) {
            let areaId: string = data.areaIds[index].objectId;
            let scheduleId: string = data.scheduleIds[index].objectId;
            let door: IAccessGroupAreaSchedule = {
                area: {
                    objectId: areaId,
                    name: data.areaIds[index].name,
                },
                schedule: {
                    objectId: scheduleId,
                    name: data.scheduleIds[index].name,
                },
            };
            doors.push(door);
        }

        interface IDataSaveParam {
            objectId?: string;
            name: string;
            remark?: string;
            doors: IAccessGroupAreaSchedule[];
        }

        let dataParam: IDataSaveParam = {
            name: data.name,
            doors,
        };

        if (data.remark) {
            dataParam.remark = data.remark;
        }

        if (data.objectId) {
            dataParam.objectId = data.objectId;
        }

        const param: { datas: IDataSaveParam[] } = { datas: [dataParam] };

        this.loading.show();

        let result: any;

        // save fake data
        let target = param.datas[0];

        if (!data.objectId) {
            ACCESS_GROUPS.push({
                objectId: Date.now().toString(),
                name: target.name,
                company: {
                    objectId: Date.now().toString(),
                    name: 'Company 5',
                },
                doors: doors,
                remark: target.remark,
            });
        } else {
            let targetIndex = ACCESS_GROUPS.findIndex((item) => item.objectId === target.objectId);

            ACCESS_GROUPS[targetIndex] = {
                objectId: target.objectId,
                name: target.name,
                company: undefined,
                doors: target.doors,
                remark: target.remark,
            };
        }

        this.loading.hide();

        ServiceToast.success(this.$i18n.Dialog_SaveSuccess);
        this.pageToList();
        await this.tableReload(this.tableParam);
    }

    private dataCUDSuccess(data: IComponent.IStatusCodeObjectIdMessageResponse[], toastMessage: string): void {
        (data || []).forEach(async (item) => {
            if (item.statusCode === 200) {
                ServiceToast.success(toastMessage);
                this.pageToList();
                await this.tableReload(this.tableParam);
            } else {
                if (item.statusCode === 401 || item.statusCode === 403) {
                    ServiceDialog.ErrorConfirmOnly(this, `${item.message}\r\n`, this.$i18n.Status_Error)
                        .then((result) => {
                            if (result.value) this.$router.push(WebPagePath.Login);
                        })
                        .catch((e: any) => console.error(e));
                } else {
                    ServiceDialog.ErrorConfirmOnly(this, `${item.message}\r\n`, this.$i18n.Status_Error)
                        .then((result) => {
                            if (result.value) return false;
                        })
                        .catch((e: any) => console.error(e));
                }
            }
        });
    }

    //////////////////// Resolve text ////////////////////
}

